.particle-background {
  display: block;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow-y: hidden;
}

.aboutme-container {
  text-align: center;
  display: flex;
  align-items: center;
  padding: 5%;
  z-index: 1;
  justify-content: center;

  @include sm {

    h1 {
      font-size: 20px;
    }

    h2 {
      font-size: 15px;
    }
  }

  @include md {

    h1 {
      font-size: 40px;
    }

    h2 {
      font-size: 25px;
    }
  }

  @include lg {

    h1 {
      font-size: 60px;
    }

    h2 {
      font-size: 25px;
    }
  }

  img {
    border-radius: 20px;
    width: 50%;
  }
}

.aboutme-img-container {
  align-items: center;

  div {
    position: relative;
    .project-icon {
      padding: 2%;
    }
  }
}